<script>
import MaxQuantResults from "../components/MaxQuantResults.vue";
import _ from "lodash";
import BlastPResults from "../components/BlastPResults.vue";
import IntensityBarchart from "../components/IntensityBarchart.vue";
import PieChartPeptides from "../components/PieChartsPeptides.vue";
import PfamResults from "../components/PfamResults.vue";
//import PhobiusResults from "../components/PhobiusResults.vue";
import PhobiusResultsPlot from "../components/PhobiusResultsPlot.vue";
import DeepLocResults from "../components/DeepLocResults.vue";
import PeptideInformation from '../components/PeptideInformation.vue';
//import Alignment from "../components/Alignment.vue";

export default {
  data() {
    return {
      headers: [],
      fragment_information: [],
      fragment: String,
      scc: String,
      molecular_weight: String,
      sequence_length: String,
      sequence_coverage: String,
      qvalue: String,
      versions: String,
      upstream: String,
      downstream: String,
      proteingroup_score: String,
      extension_sequence: String,
      no_of_peptides: String,
      alignment: [],
      offtargets: [],
      proteinId: "",
      protein_name: String,
      gene_name: String,
    };
  },
  components: {
    MaxQuantResults,
    BlastPResults,
    IntensityBarchart,
    PieChartPeptides,
    PfamResults,
    //PhobiusResults,
    PhobiusResultsPlot,
    DeepLocResults,
     PeptideInformation,
    //Alignment,
  },
  mounted(){
    const projectID = this.$route.params.projectID;
    //
    let project_information = this.$store.state.data;
    this.experiment_information = [];

    const project = project_information[projectID];
    const fragment_information = project.fragment_information;
    const id = parseInt(this.$route.params.id);
    const entry = fragment_information.find((x) => x.id === id);
    this.proteinId = entry.proteinid;
    let protein_name = entry.protein_name.split(";")[0];
    protein_name = protein_name.split("|")[1];
    protein_name = protein_name.split("[")[0];
    this.protein_name = protein_name;
    this.gene_name = entry.genename;
    this.fragment = entry["peptide"];
    this.hits = entry["protein_name"].split(";");
    const stopcodon = entry["stopcodon"];
    let downstream = entry["downstream"];
    if (downstream == "error") {
      this.scc = stopcodon + " ";
    } else {
      this.scc = stopcodon + " " + downstream.slice(0, 4);
    }
    this.score = entry["score"]; //.replace(".", ",")
    this.molecular_weight = entry["molecular_weightkda"];
    this.sequence_length = entry["sequence_lengthaa"];
    this.sequence_coverage = entry["sequence_coverage"];
    this.qvalue = entry["qvalue"];
    let versions = entry["versions"].replace("[", "").replace("]", "");
    if (versions) {
      this.versions = versions;
    } else {
      this.versions = "None";
    }

    this.upstream = entry["upstream"];
    this.downstream = downstream;
    this.proteingroup_score = entry["proteingroup_score"];
    this.delta_score = entry["delta_score"];
    this.missed_cleavages = entry["missed_cleavages"];
    this.extension_sequence = entry["extension_sequence"];
    this.no_of_peptides = entry["no_of_peptides"];
    let alignment = entry["alignment"];
    let alignment_lines = alignment.split("\n");
    let alignments = [];
    for (let line of alignment_lines) {
      line = line.replace("\r", "");
      line = line.replace('"', "");
      alignments.push(line);
    }
    alignments = alignments.slice(0, -1);
    //console.log(alignments);

    let sequence_A = entry["sequenceA"];
    let alignment_line1 = alignments[0];
    let alignment_line2 = alignments[1];
    let alignment_line3 = alignments[2];
    const range = _.range(0, sequence_A.length + 110, 110);
    let align = [];
    for (const i of range) {
      align.push(
        sequence_A.slice(i, i + 110),
        alignment_line1.slice(i, i + 110),
        alignment_line2.slice(i, i + 110),
        alignment_line3.slice(i, i + 110)
      );
    }
    this.alignment = align;
    //console.log(align);
    let off = entry["offtargets"];
    if (off == "[]") {
      this.offtargets = "none";
    } else {
      this.offtargets = off;
    }
  },
};
</script>
