export function exportExcelTable(el, filename) {
    const table = el.getElementsByTagName('table')[0];
    let content = table.outerHTML;
    content = content.replaceAll('class="trf-number-cell"', 'style="mso-number-format: \'#,##0.00\';"');
    content = '<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>' + filename + '</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body>' + content + '</body></html>';
    const link = document.createElement('a');
    link.href = 'data:application/vnd.ms-excel;base64,' + window.btoa(content);
    link.download = filename + '.xls';
    link.click();
}
